export const getCitasFiltered = ( array,code='',patient='',date='',time='',consultoryRoom='',doctor='',treatment='' ) => {
    return array.filter( item => item.id.toString().includes(code) && ( item.cita.paciente.nombre.toLowerCase().includes(patient.toLowerCase()) || item.cita.paciente.apellido.toLowerCase().includes(patient.toLowerCase()) ) && item.cita.fecha.toLowerCase().includes(date.toLowerCase()) && item.cita.hora.toLowerCase().includes(time.toLowerCase()) && ( item.cita.consultorio.numero.toLowerCase().includes(consultoryRoom.toLowerCase()) || item.cita.consultorio.nombre.toLowerCase().includes(consultoryRoom.toLowerCase()) ) && ( item.cita.doctor.nombre.toLowerCase().includes(doctor.toLowerCase()) || item.cita.doctor.apellido.toLowerCase().includes(doctor.toLowerCase()) ) && item.cita.tratamiento.toLowerCase().includes(treatment.toLowerCase()) );
}